<template>
  <el-container>
    <el-header>
      <el-menu :default-active="menu.activeIndex" class="el-menu-demo" mode="horizontal" @select="handleSelect">
        <el-menu-item index="1">中心</el-menu-item>
        <el-menu-item index="2">边缘</el-menu-item>
        <el-menu-item index="3">关于</el-menu-item>
        <el-menu-item index="4">说明</el-menu-item>
      </el-menu>
    </el-header>
    <el-main>
      <el-row :gutter="20">
        <el-col :span="12" :offset="6">
          <div class="grid-content bg-purple">
            MOLE管理系统
            <el-form ref="form" :model="form" label-width="80px">
              <el-input
                v-model="form.username"
                placeholder="用户名"
                prefix-icon="el-icon-mobile-phone"
              ></el-input>
              <el-input
                v-model="form.password"
                placeholder="密码"
                prefix-icon="el-icon-edit"
              ></el-input>
              <el-button type="primary" @click="onSubmit">登陆</el-button>
            </el-form>
          </div>
        </el-col>
      </el-row>
    </el-main>
  </el-container>
</template>

<script>
import axios from 'axios'

export default {
  data () {
    return {
      form: {
        username: '',
        password: ''
      },
      menu: {
        activeIndex: '1'
      }
    }
  },
  created: function () {
    if (sessionStorage.getItem('userInfo')) {
      this.$router.push('/dashboard')
    }
  },
  methods: {
    onSubmit () {
      axios.post('/?m=login', {
        'username': this.form.username,
        'password': this.form.password
      }).then(response => {
        if (response.data.code === 0) {
          this.$message({
            message: '登陆成功..',
            type: 'success'
          })
          sessionStorage.setItem('userInfo', JSON.stringify(response.data.data))
          this.$router.push('/dashboard')
        } else {
          this.$message.error(response.data.message)
        }
      })
    },
    handleSelect (key, keyPath) {
      console.log(key, keyPath)
    }
  }
}
</script>

<style>
  .el-col {
    border-radius: 4px;
  }

  .bg-purple-dark {
    background: #99a9bf;
  }

  .bg-purple {
    background: #dddddd;
  }

  .bg-purple-light {
    background: #e5e9f2;
  }

  .grid-content {
    border-radius: 4px;
    min-height: 36px;
  }

  .row-bg {
    padding: 10px 0;
    background-color: #f9fafc;
  }
</style>



